@font-face {
	font-family: NanumSquareOTF;
	src: url('../styles/fonts/NanumSquareOTFR.ttf') format('truetype');
}

.footer {
    margin-top: 8rem;

    .border-line {
        padding-top: 4rem;
        height: 7rem;
        background-color: #222;;
        display: flex;
        width: 100%;
        justify-content: center;

        .footer-tab {
            position: absolute;
            color: #999696;
            font-size: 1.1rem;
            font-family: NanumSquareOTF;
            font-weight: 600;
        }

        .footer-text {
            display: flex;
            color: #999696;
            font-size: 1.1rem;
            font-family: NanumSquareOTF;
            font-weight: 600;
            margin-top: 2rem;
            margin-right: 0.7rem;
            margin-left: 0.7rem;
        }
    }
}